import React, { FC } from 'react';

interface BoxProps {
  id: string;
  row: number;
  column: number;
  selectBox: (row: number, column: number) => void;
  className: 'box on' | 'box off';
}

const Box: FC<BoxProps> = ({ id, className, selectBox }) => (
  <div className={className} id={id} onClick={() => selectBox(row, column)} />
);

export default Box;
